body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #eee;
  text-align: center;
}

.ColorBlocks {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: center;
      justify-content: center;

  border-bottom: 1px solid #000;
  padding-bottom: 30px;
  margin-bottom: 15px;
}

.ColorBlocks:last-of-type {
  border-bottom: none;
  padding-bottom: 0;
  margin-bottom: 0;
}

.ColorBlock {
  width: 100px;
  height: 100px;
  border: 1px solid #000;
  margin: 15px;
}




